
.contentClient  {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    padding: 1.5rem;
    

    gap: 2rem;

    color: white;
    font-size: 1rem;
    font-weight: 400;
    text-align: center;

}

.logoClient {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.75rem;


}

.logoClient img {
    height: 4rem;
    width: 6rem;
    border-radius: 0.75rem;
    background: white;
}   